	module.exports = " <div class=button-box v-cloak> <h1>Buttons</h1> <p>任何带有<span class=point-text> class .btn </span>的元素都会继承默认按钮的外观。元素可以是<span class=point-text> a、 button、 div、 span </span>等一些常见标签。另外提供一些选项来定义按钮的样式。 </p> <p>下面介绍几种常见的按钮用法。</p> <h3>按钮大小</h3> <p>按钮的大小由<span class=point-text> .btn-lg .btn-sm </span>控制, 如大按钮需设置<span class=point-text> class=\"btn btn-lg\" </span>, 小按钮需设置<span class=point-text> class=\"btn btn-sm\" </span>。</p> <p>代码示例:</p> <div class=code> <div class=examples> <span class=d>&lt;div</span> <span class=cn>class=</span><span class=cv>\"btn btn-lg\"</span><span class=d>&gt;</span><span class=v>大按钮</span><span class=d>&lt;/div&gt;</span><br> <span class=d>&lt;div</span> <span class=cn>class=</span><span class=cv>\"btn\"</span><span class=d>&gt;</span><span class=v>按钮</span><span class=d>&lt;/div&gt;</span><br> <span class=d>&lt;div</span> <span class=cn>class=</span><span class=cv>\"btn btn-sm\"</span><span class=d>&gt;</span><span class=v>小按钮</span><span class=d>&lt;/div&gt;</span> </div> </div> <p>效果图:</p> <div class=examples> <div class=button-widgets> <div class=\"btn btn-lg\">大按钮</div> <div class=btn>按钮</div> <div class=\"btn btn-sm\">小按钮</div> </div> </div> <h3>镂空按钮</h3> <p>只需增设 <span class=point-text> .btn-hollow </span>即可。</p> <p>代码示例:</p> <div class=code> <div class=examples> <span class=d>&lt;div</span> <span class=cn>class=</span><span class=cv>\"btn btn-hollow\"</span><span class=d>&gt;</span><span class=v>按钮</span><span class=d>&lt;/div&gt;</span> </div> </div> <p>效果图:</p> <div class=examples> <div class=button-widgets> <div class=\"btn btn-hollow btn-lg\">按钮</div> <div class=\"btn btn-hollow\">按钮</div> <div class=\"btn btn-hollow btn-sm\">按钮</div> </div> </div> <h3>带图按钮</h3> <p>普通带图按钮, 设置<span class=point-text> class=\"btn btn-icon\" </span>即可。</p> <p>镂空风格带图按钮, 设置<span class=point-text> class=\"btn btn-hollow-icon\" </span>即可。</p> <p>也可给<span class=point-text> i </span>标签设置<span class=point-text> class=\"btn-icon-i\" </span>, <span class=point-text> span </span>标签设置<span class=point-text> class=\"btn-icon-text\" </span>。</p> <p>代码示例:</p> <div class=code> <div class=examples> <span class=d>&lt;div</span> <span class=cn>class=</span><span class=cv>\"btn btn-icon\"</span><span class=d>&gt;&lt;i&gt;&lt;/i&gt;&lt;span&gt;</span><span class=v>带图按钮</span><span class=d>&lt;/span&gt;&lt;/div&gt;</span><br> <span class=d>&lt;div</span> <span class=cn>class=</span><span class=cv>\"btn btn-hollow-icon\"</span><span class=d>&gt;&lt;i&gt;&lt;/i&gt;&lt;span&gt;</span><span class=v>带图按钮</span><span class=d>&lt;/span&gt;&lt;/div&gt;</span> </div> </div> <p>效果图:</p> <div class=examples> <div class=button-widgets> <div class=\"btn btn-icon\"><i class=btn-icon-i></i><span class=btn-icon-text>带图按钮</span></div> <div class=\"btn btn-hollow-icon\"><i class=btn-icon-i></i><span class=btn-icon-text>带图按钮</span></div> </div> </div> <h3>失效按钮</h3> <p>必需设置<span class=point-text> class=\"btn btn-forbidden\" </span>。</p> <p>另外可使用<span class=point-text> .btn-lg .btn- sm </span>控制大小。</p> <div class=examples> <div class=button-widgets> <div class=\"btn btn-forbidden btn-lg\">失效按钮</div> <div class=\"btn btn-forbidden\">失效按钮</div> <div class=\"btn btn-forbidden btn-sm\">失效按钮</div> </div> </div> </div> ";

